/* effects */
@keyframes fadeInUp {
  from {
    transform: translate3d(0, 100%, 0);
    opacity: 0; }
  to {
    transform: translate3d(0, 0, 0);
    opacity: 1; } }

.parallax {
  will-change: transform; }

.with-overlay {
  position: relative; }
  .with-overlay::before, .with-overlay::after {
    position: absolute;
    right: 0;
    left: 0;
    height: 25em;
    content: '';
    pointer-events: none; }
  .with-overlay::before {
    top: -1px;
    background: linear-gradient(#032a84, rgba(3, 42, 132, 0)); }
  .with-overlay::after {
    bottom: -1px;
    background: linear-gradient(rgba(3, 42, 132, 0), #032a84); }
  .with-overlay.overlay-zindex_1::before, .with-overlay.overlay-zindex_1::after {
    z-index: 1; }

