.stars {
  display: flex;
  position: relative;
  flex-direction: row-reverse;
  justify-content: center;
  border-bottom: 1px solid currentColor;
  padding-bottom: .5em;
  font-size: 7rem; }
  .stars [type="radio"] {
    display: none; }
  .stars .stars_star {
    position: relative;
    cursor: pointer;
    text-align: center;
    font-weight: bold; }
  .stars .stars_star::before {
    display: block;
    margin: 0 .3em;
    background-image: url("assets/star.svg");
    background-repeat: no-repeat;
    background-size: contain;
    width: 1em;
    height: 1em;
    content: " "; }
  .stars .stars_star:hover::before,
  .stars .stars_star:hover ~ .stars_star::before,
  .stars [type="radio"]:checked ~ .stars_star::before {
    background-image: url("assets/star-filled.svg"); }
  .stars [type="radio"]:checked + .stars_star::after {
    position: absolute;
    top: .8em;
    left: 50%;
    border-left: 1px solid currentColor;
    width: 1px;
    height: 0.7em;
    content: '';
    pointer-events: none; }
  .stars [type="radio"]:checked + .stars_star + .stars_title {
    opacity: 1; }
  .stars [type="radio"]:checked + .stars_star + .stars_title + .stars_description {
    opacity: 1; }
  .stars .stars_title {
    position: absolute;
    top: 6em;
    transition: opacity 0.2s;
    opacity: 0;
    text-transform: uppercase;
    font-size: 0.2em;
    font-weight: bold; }
    .stars .stars_title:nth-of-type(5) {
      left: 3em; }
    .stars .stars_title:nth-of-type(4) {
      left: 11em; }
    .stars .stars_title:nth-of-type(3) {
      left: 19em; }
    .stars .stars_title:nth-of-type(2) {
      right: 11em; }
    .stars .stars_title:nth-of-type(1) {
      right: 3em; }
  .stars .stars_description {
    position: absolute;
    top: 11.5em;
    left: 0;
    transition: opacity 0.2s;
    opacity: 0;
    font-size: 1rem; }

